import { useState } from "react";
import { byTitle } from "../../helpers/inventory";
import "./styles.index.css";

const Product = ({ title, cart, setCart }) => {
  const [count, setCount] = useState(0);
  const beers = byTitle(title);

  const handleAdd = () => {
    setCount(count + 1);
    setCart([...cart, title]);
  }

  const handleRemove = () => {
    if (count > 0) {
      setCount(count - 1);
      const index = cart.indexOf(title);
      if (index > -1) {
        delete cart[index];
      }
      setCart([...cart]);
    }
  }

  return (
    <div className="Product">
      <h3>{title}</h3>
      <div className="wrapper">
        { beers.map(beer => {
          return <><span className="beer-quantity">{`${beer["quantity"]}`}x </span><span className="beer-name">{`${beer["beer"]}`}</span><br/></>;
        }) }
      </div>
      <div>
        <button onClick={handleAdd}>Add</button>
        <button onClick={handleRemove}>Remove</button>
        <span className="case-amount">{count}</span>
      </div>
    </div>
  );
};

export default Product;
