import { useState } from "react";
import { uniqueTitles } from "./helpers/inventory";
import Cart from "./components/Cart";
import Product from "./components/Product";
import './App.css';

function App() {
  const [cart, setCart] = useState([]);
  const uniques = uniqueTitles({type: "mixed"});

  return (
    <div className="App-wrapper row">
      <div className="App column">
        {uniques.map(value => {
          return (<><Product title={value} cart={cart} setCart={setCart} /><hr/></>);
        })}
      </div>
      <div className="column">
        <div className="Cart">
          <Cart datas={cart} />
        </div>
      </div>
    </div>
  );
}

export default App;
