import DATAS from "../datas/options";

const byType = type => {
  return DATAS.filter(data => data["type"] === type);
}

const uniqueTitles = (options = {}) => {
  let datas = DATAS;
  if (options["type"]) {
    datas = byType(options["type"]);
  }

  const titles = {};
  datas.forEach(data => {
    titles[data["title"]] = 1;
  });
  return Object.keys(titles);
}

const byTitle = title => {
  return DATAS.filter(data => data["title"] === title);
}

const sortByKey = key => {
  const compare = (a, b) => {
    return a[key].localeCompare(b[key]);
  };

  return compare;
}


export { byTitle, byType, sortByKey, uniqueTitles };
