import { useEffect, useState } from "react";
import { byTitle } from "../../helpers/inventory";
import "./styles.index.css";

const Cart = ({ datas }) => {
  const [beerList, setBeerList] = useState([]);

  useEffect(() => {
    const newList = {};

    datas.forEach(data => {
      const beerByTitle = byTitle(data);
      beerByTitle.forEach(bbt => {
        if (!newList[bbt["beer"]]) {
          newList[bbt["beer"]] = 0;
        }

        newList[bbt["beer"]] += parseInt(bbt["quantity"]);
      });
    });

    setBeerList(newList);
  }, [datas]);

  return (
    <>
      <h3>Cart</h3>
      <ul>
        {Object.keys(beerList).sort().map(value => {
          return <li>{`${value} x${beerList[value]}`}</li>;
        })}
      </ul>
    </>
  );
};

export default Cart;
